import React, { Component } from 'react'

class SideEducation extends Component {

    render() {

        return (
            <main id="side-ed-main" className="side-block">

                <h5 className="side-category">Education</h5>

                <h6 className="side-subheader">School</h6>
                <ul className="side-category">
                    <li>The University of Texas at Austin</li>
                </ul>

                <h6 className="side-subheader">Major</h6>
                <ul className="side-category">
                    <li>Electrical and Computer Engineering</li>
                </ul>

                <h6 className="side-subheader">Overall GPA</h6>
                <ul className="side-category">
                    <li>3.84/4.00</li>
                </ul>

                <h6 className="side-subheader">Relevant Coursework</h6>
                <ul className="side-category">
                    <li>Operating Systems</li>
                    <li>Algorithms</li>
                    <li>Software Design and Implementation II</li>
                    <li>Digital Logic Design</li>
                </ul>


            </main>
        )
    }
}

export default SideEducation