import React, { Component } from 'react'
import './LearnMore.css'

class LearnMore extends Component {

    constructor(props) {
        super(props);
        this.state = {
            logo: props.logo
        }
    }

    render() {

        const Image = ({ className, img }) => (
            <div className="img-wrapper">
                <img src={img} className="embedded-img" />
            </div>
        )

        const website = this.props.website !== null &&
            <a type="button" className="btn landing-btn mr-auto learn-more-btn" href={this.props.website}>
                Visit Site
            </a>

        let githubLinks = null
        if (this.props.github !== null)
            githubLinks = this.props.github.map((link) =>
                <a key={link.link} type="button" className="btn landing-btn learn-more-btn link-btn" href={link.link}>
                    {"Github" + (link.name === null ? "" : ": " + link.name)}
                </a>
            );

        let img = null
        if (this.props.images !== null)
            img = <Image className="learn-img" img={this.props.images[0]} />

        return (

            <div className="modal fade mw-100" id={this.props.modalId} tabIndex="-1" role="dialog" aria-hidden="true">
                <div className="modal-dialog modal-lg" role="document">
                    <div className="modal-content">
                        <div className="modal-body">

                            {img}
                            <div className="learn-details">
                                <h5 className="modal-title">{this.props.companyName}</h5>

                                <div className="mt-3">

                                    {this.props.position !== null &&
                                        <h6>
                                            {this.props.position}
                                        </h6>
                                    }
                                    <p><i>{this.props.date}</i></p>

                                    <p>
                                        {this.props.description}
                                    </p>
                                </div>

                            </div>
                        </div>
                        <div className="modal-footer">

                            <span className="mr-auto">

                                {this.props.website !== null && website}
                                {this.props.github !== null && githubLinks}
                            </span>
                            <button type="button" className="close btn px-3 py-3" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default LearnMore