import React, { Component } from 'react'
import SideBar from './SideBar'
import About from './About'
import Employment from './Employment'
import Projects from './Projects'
import SideContactMe from './SideContactMe'
import SideCredits from './SideCredits'
import "./Summary.css"

class Landing extends Component {

    render() {

        return (
            <div id="summary-main" className="mw-100">
                <div className="col-12 col-lg-3 float-left overflow-hidden p-0 sidebar">
                    <SideBar />
                </div>
                <div className="p-0 minh-100">
                    <About />
                    <Employment />
                    <Projects />
                    <div className="show-sm sidebar-dark">
                        <SideContactMe />
                        <SideCredits />
                    </div>
                </div>
            </div>
        )
    }
}

export default Landing