import React, { Component } from 'react'
import Job from "./Job"
import LearnMore from "./LearnMore"
import "./Employment.css"

class Employment extends Component {

    constructor(){
        super();
        this.toModalId = this.toModalId.bind(this);
    }

    toModalId(companyName){
        return "modal-" + companyName.replace(/\s+/g, '-').toLowerCase()
    }

    render() {

        let utName = "The University of Texas at Austin"
        let utrName = "UT Review"
        let equinorName = "Equinor"

        let utId = this.toModalId(utName);
        let utrId = this.toModalId(utrName);
        let equinorId = this.toModalId(equinorName);

        let ut_logo = require("./../img/ut-logo.jpg")
        let utreview_logo = require("./../img/utreview_logo.png")
        let equinor_logo = require("./../img/equinor_logo.png")
        
        let jobs = [
            {
                companyName:utName, 
                date:"Oct 2020 - Present", 
                position: "Software Implementation Tutor",
                description: "Tutors University of Texas at Austin students through Software Design and Implementation course.",
                skills: ["C++"],
                website: null,
                github: null,
                modalId:utId, 
                logo:ut_logo, 
                images: null,
            },
            {
                companyName:utrName, 
                date:"May 2020 - Present", 
                position: "Co-Founder, Frontend/Backend Developer",
                description: `Centralized UT course and professor information for users to leave reviews and compare grade distributions, scheduling times, CIS scores, and more.
                Worked with other student-led projects such as UT Registration Plus. 
                Automated backend scraping, downloading, and parsing for most up-to-date information available.
                Attained over 30 signups, 120 reviews, 150 users, and 1,000 pageviews during the first week of launch.`,
                skills: ["Web Development", "JavaScript", "Python", "ReactJS", "Flask", "HTML", "CSS", "Linux", "MySQL", "AWS"],
                website: "https://www.utexasreview.com/",
                github: [
                    {name: "Frontend", link:"https://github.com/KiboNaku/utreview-frontend"}, 
                    {name: "Backend", link:"https://github.com/KiboNaku/utreview-backend"}],
                modalId:utrId, 
                logo:utreview_logo, 
                images: [require("./../img/utreview-img-3.PNG")],
            },
            {
                companyName:equinorName, 
                date:"June 2020 - July 2020", 
                position: "IT Data and Management Intern",
                description: `Developed module to automate downloading and parsing from an unstructured database with over 5 Terabytes of data containing files with formats such as .pdf, .xml, and .html.
                Worked with the team to train/test machine learning models to impact decisions during live drilling.
                Implemented Machine Learning algorithms (Random Forest Classifier, LSTM) and SMOTE oversampling to predict drilling interruptions up to 2.5 hours in advance.`,
                skills: ["Python", "Data Parsing", "Machine Learning"],
                website: null,
                github: null,
                modalId:equinorId, 
                logo:equinor_logo,
                images: null,
            }
        ]
        

        return (
            <main id="about-main" className="employment">

                <div className="d-inline">

                    <div className="content-panel content-panel-bg">

                        <div>
                            <h2 className="heading-title">Employment</h2>
                            <p>(hover or tap for more info)</p>
                        </div>

                        <Job modalId={utrId} logo={utreview_logo} companyName={utrName} positionName="Co-Founder, Frontend/Backend Developer" date="May 2020 - Present"/>
                        <Job modalId={utId} logo={ut_logo} companyName={utName} positionName="Software Implementation Tutor" date="Oct 2020 - Present"/>
                        <Job modalId={equinorId} logo={equinor_logo} companyName={equinorName} positionName="IT and Data Management Intern" date="June 2020 - July 2020"/>

                    </div>
                </div>

                <LearnMore {...jobs[0]}/>
                <LearnMore {...jobs[1]}/>
                <LearnMore {...jobs[2]}/>
            </main>
        )
    }
}

export default Employment