import React, { Component } from 'react'
import SideEducation from './SideEducation'
import SideSkills from './SideSkills'
import SideContactMe from './SideContactMe'
import SideCredits from './SideCredits'
import './SideBar.css'

class SideBar extends Component {

    render() {

        return (
            <main id="sidebar-main" className="h-100 sidebar-dark">

                <SideEducation />
                <SideSkills />

                <div className="show-lg">
                    <SideContactMe />
                    <SideCredits />
                </div>
            </main>
        )
    }
}

export default SideBar