import React, { Component } from 'react'

class SideCredits extends Component {

    render() {

        return (
            <main id="contact-me-main" className="side-block">

                <h5 className="side-category">Credits</h5>
                <h6 className="side-subheader">Icons (GitHub, Mail, and LinkedIn)</h6>
                <ul className="side-category">
                    <li>
                        Made by <a href="https://www.flaticon.com/authors/pixel-perfect"
                            title="Pixel perfect">Pixel perfect</a> from <a href="https://www.flaticon.com/" title="Flaticon"> www.flaticon.com</a>
                    </li>
                </ul>
            </main>
        )
    }
}

export default SideCredits