import React, { Component } from 'react'

class SideSkills extends Component {

    render() {

        return (
            <main id="side-skills-main" className="side-block">

                <h5 className="side-category">Skills</h5>

                <h6 className="side-subheader">Experienced</h6>
                <ul className="side-category">
                    <li>Java</li>
                    <li>Python</li>
                    <li>JavaScript</li>
                    <li>ReactJS</li>
                    <li>Flask</li>
                    <li>Android Studio</li>
                    <li>Web Development</li>
                    <li>Web Scraping</li>
                </ul>

                <h6 className="side-subheader">Familiar</h6>
                <ul className="side-category">
                    <li>C</li>
                    <li>C++</li>
                    <li>HTML</li>
                    <li>CSS</li>
                    <li>Linux</li>
                    <li>Git</li>
                    <li>JUnit</li>
                </ul>

                <h6 className="side-subheader">Exposed</h6>
                <ul className="side-category">
                    <li>R</li>
                    <li>C#</li>
                    <li>MySQL</li>
                    <li>jQuery</li>
                    <li>ARM Assembly</li>
                    <li>AWS</li>
                    <li>Machine Learning</li>
                </ul>
            </main>
        )
    }
}

export default SideSkills