import React, { Component } from 'react'
import Project from './Project'
import LearnMore from './LearnMore'

class Projects extends Component {

    constructor() {
        super();
        this.toModalId = this.toModalId.bind(this);
    }

    toModalId(companyName) {
        return "modal-" + companyName.replace(/\s+/g, '-').toLowerCase()
    }

    render() {

        let cbn = require("./../img/color_by_number_2.jpg");
        let pfp = require("./../img/food_picker.jpg");
        let pbb = require("./../img/pokemon_boba_1.PNG");
        let sn = require("./../img/sticky_notes.png");
        let wu = require("./../img/website_updater.png");
        let rc = require("./../img/robot_car.jpg");

        let cbpName = "Color by Photo";
        let pfpName = "Project Food Picker";
        let pbbName = "Pokemon Boba Black";
        let snName = "Sticky Notes";
        let wuName = "Website Updater";
        let rcName = "Robot Car";

        let cbpId = this.toModalId(cbpName);
        let pfpId = this.toModalId(pfpName);
        let pbbId = this.toModalId(pbbName);
        let snId = this.toModalId(snName);
        let wuId = this.toModalId(wuName);
        let rcId = this.toModalId(rcName);

        let projects = [
            {
                companyName: cbpName,
                date: "Mar 2020 - Mar 2020",
                position: null,
                description: "Developed an Android application that converts photos to color-by-number interactive pages.",
                skills: ["Java", "Android Studio"],
                website: null,
                github: [{ name: null, link: "https://github.com/KiboNaku/ColorByNumber" }],
                modalId: cbpId,
                logo: cbn,
                images: [cbn],
            },
            {
                companyName: pfpName,
                date: "Dec 2019 - Jan 2020",
                position: null,
                description: "Developed an Android application that random selects nearby restaurants based on preferences.",
                skills: ["Java", "Android Studio"],
                website: null,
                github: [{ name: null, link: "https://github.com/KiboNaku/Project-Food-Picker" }],
                modalId: pfpId,
                logo: pfp,
                images: [pfp],
            },
            {
                companyName: pbbName,
                date: "Nov 2019 - Dec 2019",
                position: null,
                description: `Planned and designed a Pokmon game with shopping, exploring, and combat mechanics on a TM4C.
                Implemented efficient and concise C code to incorporate walking Pokmon, a scrollable map, and over 20 unique monsters, 20 sprites, 18 types, and 
                50 unique moves on a device with 256kb of memory.
                Received 4th place in classroom competition with over 70 students.`,
                skills: ["C"],
                website: null,
                github: [{ name: null, link: "https://github.com/EE319K-F19/Pokemon-Boba-Black" }],
                modalId: pbbId,
                logo: pbb,
                images: [pbb],
            },
            {
                companyName: snName,
                date: "June 2019 - June 2019",
                position: null,
                description: "Developed a sticky notes application using JavaFX.",
                skills: ["Java", "JavaFX"],
                website: null,
                github: [{ name: null, link: "https://github.com/KiboNaku/StickyNotes" }],
                modalId: snId,
                logo: sn,
                images: [sn],
            },
            {
                companyName: wuName,
                date: "June 2019 - June 2019",
                position: null,
                description: "Developed a JavaFX application to automate updating a static website.",
                skills: ["Java", "JavaFX"],
                website: null,
                github: null,
                modalId: wuId,
                logo: wu,
                images: [wu],
            },
            {
                companyName: rcName,
                date: "May 2019 - May 2019",
                position: null,
                description: "Developed an autonomous robot car that can either follow a line or move between static walls.",
                skills: ["Arduino", "C"],
                website: null,
                github: null,
                modalId: rcId,
                logo: rc,
                images: [rc],
            }
        ]

        return (
            <main id="about-main" className="projects">

                <div className="d-inline">

                    <div className="content-panel content-panel-bg">

                        <div>
                            <h2 className="heading-title">Projects</h2>
                            <p>(hover or tap for more info)</p>
                        </div>

                        <Project modalId={cbpId} logo={cbn} companyName="Color by Photo" date="Mar 2020 - Mar 2020" />
                        <Project modalId={pfpId} logo={pfp} companyName="Project Food Picker" date="Dec 2019 - Jan 2020" />
                        <Project modalId={pbbId} logo={pbb} companyName="Pokemon Boba Black" date="Nov 2019 - Dec 2019" />
                        <Project modalId={snId} logo={sn} companyName="Sticky Notes" date="June 2019 - June 2019" />
                        <Project modalId={wuId} logo={wu} companyName="Website Updater" date="June 2019 - June 2019" />
                        <Project modalId={rcId} logo={rc} companyName="Robot Car" date="May 2019 - May 2019" />

                    </div>
                </div>

                <LearnMore {...projects[0]} />
                <LearnMore {...projects[1]} />
                <LearnMore {...projects[2]} />
                <LearnMore {...projects[3]} />
                <LearnMore {...projects[4]} />
                <LearnMore {...projects[5]} />
            </main>
        )
    }
}

export default Projects