import React, { Component } from 'react'
import styled from "@emotion/styled/macro";
import $ from 'jquery'
import "./Job.css"

class Project extends Component {

    constructor() {
        super();
        this.state = {
            isHovering: false,
        };
    }

    render() {

        const Image = ({ className }) => (
            <div className={className} style={{backgroundImage: `url(${this.props.logo})`}}></div>
        )

        const Default = styled.div({
            opacity: 1,
            transition: "opacity 350ms ease",
        });

        const DefaultContent = () => (
            <div className="center-holder w-100 mh-100 h-100">

                <Image className="logo-img proj-image" />
            </div>
        )

        const DisplayOver = styled.div({
            height: "100%",
            left: "0",
            position: "absolute",
            top: "0",
            width: "100%",
            zIndex: 2,
            transition: "background-color 350ms ease",
            backgroundColor: "transparent"
        });

        const Hover = styled.div({
            backgroundColor: "white",
            height: "100%",
            width: "100%",
            opacity: 0,
            zIndex: 1000000,
            display: "flex",
            transform: "translate3d(0,50px,0)",
            transition: "opacity 350ms ease",
            alignItems: "center",
        });

        const HoverContent = () => (
            <div className="center-holder w-100">
                <h5>{this.props.companyName}</h5>
                <p>
                    {this.props.positionName}
                </p>
                <p><i>{this.props.date}</i></p>

                <button onClick={()=>{$("#" + this.props.modalId).modal();}} className="job-btn-dark">Learn more</button>
            </div>
        )

        const Background = styled.div({
            [`:hover ${Default}`]: {
                opacity: 0,
                display: "none",
            },
            [`:hover ${Hover}`]: {
                display: "flex",
                opacity: 1,
                transform: "translate3d(0,0,0)",
            },
        });

        return (

            <div className="project col-12 col-sm-6 col-md-4 d-inline-block py-0 my-0">


                <Background>

                    <DisplayOver>
                        <Default>
                            <DefaultContent />
                        </Default>
                        <Hover>
                            <HoverContent />
                        </Hover>
                    </DisplayOver>
                </Background>
            </div >
        )
    }
}

export default Project